@font-face {
  font-family: "Roboto";
  font-weight: normal;
  font-style: normal;
  src: url("fonts/Roboto/RobotoRegular.eot");
  src: url("fonts/Roboto/RobotoRegular.eot?#iefix") format("embedded-opentype"), url("fonts/Roboto/RobotoRegular.woff") format("woff"), url("fonts/Roboto/RobotoRegular.ttf") format("truetype");
}

@font-face {
  font-family: "Roboto";
  font-weight: bold;
  font-style: normal;
  src: url("fonts/Roboto/RobotoBold.eot");
  src: url("fonts/Roboto/RobotoBold.eot?#iefix") format("embedded-opentype"), url("fonts/Roboto/RobotoBold.woff") format("woff"), url("fonts/Roboto/RobotoBold.ttf") format("truetype");
}

@font-face {
  font-family: "Segoe UI";
  font-weight: normal;
  font-style: normal;
  src: url("fonts/SegoeUI/SegoeUI.eot");
  src: url("fonts/SegoeUI/SegoeUI.eot?#iefix") format("embedded-opentype"), url("fonts/SegoeUI/SegoeUI.woff") format("woff"), url("fonts/SegoeUI/SegoeUI.ttf") format("truetype");
}

.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  fill: currentColor;
  stroke: currentColor;
  stroke-width: 0;
}

.icon-arrow-up {
  font-size: 7rem;
  width: 1em;
}

body, div, dl, dt, dd, ul, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, input, textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset, img, abbr {
  border: 0;
}

address, caption, cite, code, dfn, em, strong, th, var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption, th {
  text-align: left;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input, textarea, select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000000;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

html, body, .l-page {
  height: 100%;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Roboto", "Segoe UI", "Verdana", sans-serif;
  font-size: 1.6rem;
  min-width: 32rem;
  line-height: 1.2;
  overflow-x: hidden;
}

body.b-popup-open {
  overflow: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
}

button:focus {
  outline: 0;
}

img {
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

.l-page {
  display: flex;
  flex-direction: column;
}

.l-container {
  flex-grow: 1;
}

.l-wrapper, .l-full {
  max-width: 1180px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
  height: 100%;
  position: relative;
}

.l-full {
  max-width: 192rem;
}

.hidden {
  opacity: 0;
  visibility: hidden;
}

#svg-sprite {
  position: absolute;
  width: 0;
  height: 0;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1%;
  margin-right: -1%;
}

.grid__item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1%;
  margin-right: 1%;
}

.grid__item--1 {
  width: 2.166666666666667%;
}

.grid__item--2 {
  width: 6.333333333333334%;
}

.grid__item--3 {
  width: 10.5%;
}

.grid__item--4 {
  width: 14.666666666666668%;
}

.grid__item--5 {
  width: 18.833333333333336%;
}

.grid__item--6 {
  width: 23%;
}

.grid__item--7 {
  width: 27.166666666666668%;
}

.grid__item--8 {
  width: 31.333333333333336%;
}

.grid__item--9 {
  width: 35.5%;
}

.grid__item--10 {
  width: 39.66666666666667%;
}

.grid__item--11 {
  width: 43.833333333333336%;
}

.grid__item--12 {
  width: 48%;
}

.grid__item--13 {
  width: 52.16666666666667%;
}

.grid__item--14 {
  width: 56.333333333333336%;
}

.grid__item--15 {
  width: 60.50000000000001%;
}

.grid__item--16 {
  width: 64.66666666666667%;
}

.grid__item--17 {
  width: 68.83333333333334%;
}

.grid__item--18 {
  width: 73%;
}

.grid__item--19 {
  width: 77.16666666666667%;
}

.grid__item--20 {
  width: 81.33333333333334%;
}

.grid__item--21 {
  width: 85.5%;
}

.grid__item--22 {
