}

.grid__item--23 {
  width: 93.83333333333334%;
}

.grid__item--24 {
  width: 98%;
}

.relation {
  position: relative;
  width: 100%;
  height: 0;
}

.relation--hd {
  padding-top: 56.25%;
}

.relation--square {
  padding-top: 100%;
}

.relation__content {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.scroll-top {
  position: fixed;
  right: 2rem;
  bottom: 2rem;
  opacity: 0.5;
}

.scroll-top__icon {
  color: #95A5A6;
}

.youtube {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
  background-color: #000000;
}

.youtube:hover .youtube__button-shape, .youtube__button:focus .youtube__button-shape {
  fill: #FF0000;
  fill-opacity: 1;
  transition: fill 0.1s cubic-bezier(0, 0, 0.2, 1), fill-opacity 0.1s cubic-bezier(0, 0, 0.2, 1);
}

.youtube--enabled {
  cursor: pointer;
}

.youtube--enabled .youtube__button {
  display: block;
}

.youtube__link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.youtube__media {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

.youtube__button {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  display: none;
  padding: 0;
  width: 68px;
  height: 48px;
  border: none;
  background-color: transparent;
  transform: translate(-50%, -50%);
  cursor: pointer;
}

.youtube__button:focus {
  outline: none;
}

.youtube__button-svg {
  width: 100%;
  height: 100%;
}

.youtube__button-shape {
  fill: #212121;
  fill-opacity: 0.8;
  transition: fill 0.1s cubic-bezier(0.4, 0, 1, 1), fill-opacity 0.1s cubic-bezier(0.4, 0, 1, 1);
}

.youtube__button-icon {
  fill: #FFFFFF;
}

@media screen and (max-width: 768px) {
  .l-wrapper, .l-full {
    padding-left: 15px;
    padding-right: 15px;
  }
}
